.Navigationbar{
    display: flex;
    justify-content: center;
    justify-items:center ;
    gap: 2%;
    
 
    background-color:transparent;
   
    border: 4px double whitesmoke;
    border-radius: 55px;

    height: 12%;
    width: 35%;
    z-index: 12;
    box-shadow: 5px 13px 15px rgba(0, 0, 0.5, 0.8);

    position: fixed;
    bottom: 50px;
    margin-top: 1%;
    margin-left: 32%;
    overflow: hidden;
    
}

.Navigationbar button{
    background-color: transparent;
    
    border: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
   

}
.Navigationbar button img{
    width: 65px;
    height: 65px;
    object-fit: fill;
    background-color: transparent;
}
.Navigationbar button:hover{
    background-color: white;
    scale: 1.05;

}

@media (max-width: 700px){
    .Navigationbar{
        width: 70%;
        margin-left: 13%;
    }
    
    
    .Navigationbar button img{
        width: 46px;
        height: 46px;
        object-fit: fill;
        background-color: transparent;
    }
    .Navigationbar button:hover{
        background-color: white;
        scale: 1.05;
    
    }

}
