/* search bar stylings */
.searchDiv {
  position: relative;
  width: 100%;
  height: 15vh;
  border-bottom: 4px solid #caebec;
  margin-top: -1%;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
 position: sticky;
 top: 7%;
 
 background-color: white;
  gap: 4%;
}

/* style for search box(its a div so we can include z search icon */
.searchDiv .search {
  display: flex;
  align-items: center;
  background-color: #e3fdfd; 
  border-radius: 35px; 
  border:2px double #0ca4a5;
  padding: 10px 15px;
  width: 80%; 
  max-width: 700px; 
  height: fit-content;
}

/*this is styling for the search text box*/
.searchDiv input {
  border: none; 
  outline: none; 
  background: transparent; 
  flex: 1; 
  font-size: 20px; 
  padding: 5px; 
  border-radius: 35px; 
 
}
.searchDiv .search img {
  width: 38px; 
  height: 34px; 
  margin-right: 10px;
}
.searchDiv select {
  width: fit-content;
  background-color: #e3fdfd;
  border-radius: 20px;
  border:2px double #0ca4a5;
  height: 40%;
  font-size: 110%;
  position: relative;
}

.searchDiv label {
  /* margin-top: 2.5%; */
  font-size: 150%;
  color: #0ca4a5;
}


@media (max-width:967px){
  .searchDiv {
    font-size: 14px;
    padding: 10px;
  }

  .searchDiv input {
    width: 70%;
    font-size: 12px;
  }
}

@media (max-width:967px){
  .searchDiv{
    top: 50px;
  }
}