.footer{
    position: relative;
    bottom: 0;
   margin-top: auto;
    
   
    width: 100%;
    color: white;
    background-color: #0ca4a5;
    /* border: 2px solid black;border for visualization(will take out later) */
    display: flex;
    justify-content: space-evenly;
    gap: 25%;
}

.footer a{
    font-size: 16px;
    padding-bottom: 10px;
    color: white;
}
.termspage{
    height: fit-content;
    width: 85vw;

    background-color: #e3fdfd;
    border: 2px solid #0ca4a5;
    border-radius: 20px;

    display: flex;
    flex-direction: column;
    /* text-align: center; */

    margin-left: 5%;
    margin-top: 4%;
}
.termspage li{
    margin-left: 24%;
    margin-bottom: 5%;
}

@media(max-width: 500px){
    .footer{
        flex-direction: column;
        margin-bottom: 0;
        padding-left: 25%;
    }
}

