.lists {
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;


    margin-top: 5%;

    height: 80%;
    width: 100%;

    overflow: auto;

    gap: 3%;


}

.lists img {
    height: 85%;
    width: 100%;
    top: 1%;
    background-color: transparent;
    border: 1px solid gray;
    position: relative;
}