/*a single card item with property info*/
.card {
  position: relative;
  background-color: #e3fdfd;
  border: 1px double #71c9ce;

  display: flex;
  flex-direction: column;
  flex: 0 0 24%;
  /* Each card added will be 27% wide */


  height: 400px;
  padding: 0.5%;
  margin-top: 1%;
  border-radius: 10px;
  margin-bottom: 1%;

  box-shadow: 14px 8px 14px rgba(0, 0, 0.5, 0.5);
  text-align: center;
}

.card p {
  font-size: 18px;
  margin-bottom: 3px;
}

.card h3 {
  margin-bottom: 5px;
}

.card:hover,
.offerCard:hover,
.eventCard:hover {
  scale: 1.05;
  transition: ease-in-out 0.2s;
  opacity: 0.9;
  background-color: #cbf1f5;
}

.card:active,
.offerCard:active,
.eventCard:active {
  background-color: #71c9ce;

}

.card #propImg {
  /* property image on the card */
  height: 100%;
  width: 100%;

  background-color: white;
  border: 1px solid gray;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: relative;
}

.propInfo {
  /*card with amount of property owned and sold in account page*/
  display: flex;
  flex-direction: column;
  justify-items: left;
  gap: -8%;


  width: 13vw;
  height: 13vh;

  margin-top: 2%;
  margin-left: 25%;
  padding-left: -2.5%;

  background-color: #71c9ce;
  border-radius: 20px;
  border-top-left-radius: 0;
  color: white;
  /* border: 2px solid black; */
}

.card h4 {
  margin-left: 2%;

}

.eventCard {
  width: 80%;

  position: relative;
  background-color: #e3fdfd;

  display: flex;
  flex-direction: column;
  flex: 0 0 24%;
  /* Each card added will be 27% wide */

  height: 90%;
  padding: 1%;
  margin-top: 1%;
  border-radius: 2%;

  box-shadow: 14px 8px 14px rgba(0, 0, 0.5, 0.5);
  text-align: center;
  animation: scroll 10s linear infinite;
  /* Animation for scrolling */
  animation-delay: 1s;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100%);
    /* Scroll left */
  }
}

.offerCard {
  width: 100%;

  position: relative;
  background-color: #e3fdfd;

  display: flex;
  flex-direction: column;
  flex: 0 0 24%;
  /* Each card added will be 27% wide */

  height: 70%;
  padding: 1%;
  margin-top: 1%;
  border-radius: 2%;

  box-shadow: 14px 8px 14px rgba(0, 0, 0.5, 0.5);
  text-align: center;
  animation: scrollBack 10s linear infinite;
  /* Animation for scrolling */
  animation-delay: 1s;
}

@keyframes scrollBack {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-100%);
    /* Scroll right */
  }
}

/* Responsivness will be handeled once i figure out the kinks of this library. */

@media(max-width:967px) {
  .card:hover {
    scale: 1.1;
    transition: ease-in-out 0.2s;
    opacity: 0.9;
    background-color: aqua;
  }

}

@media(max-width:600px) {
  .card {
    width: 50%;
    height: 80%;
  }

}