.pay{
    padding: 20px;
  }
  
  .pay  .app {
      max-width: 600px;
      margin: 20px auto;
      padding: 20px;
      border: 1px solid #ccc;
      border-radius: 8px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
    
    .pay  h1, .pay  h2 {
      text-align: center;
    }
    
    .pay  form {
      display: flex;
      flex-direction: column;
    }
    
   .pay  input,  .pay textarea, .pay button {
      margin: 10px 0;
      padding: 10px;
    }
  
    .pay   button[type='submit']{
      background-color: #0ca4a5;
      color: white;
    }