.HomePage {
    width: 100vw;
  
    position: relative;

    display: flex;
    flex-direction: column;
    gap: 1em;
   
    top: 0;
    left: 0;


}

.searchSection { 
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;
    text-align: center;
    justify-items: center;

    background-image: url("../images/Homebg.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.searchbar {
    margin-top: 18%;
    margin-left: 20%;

    width: 60%;
    height: 7%;
    background-color: #e3fdfd;

    border-radius: 30px;

    display: flex;
    flex-direction: row;


}

.searchbar input {
    border: none;
    height: 100%;
    width: 100%;

    background-color: transparent;
    color: #2596be;
    flex-wrap: wrap;


    font-size: 24px;

    text-align: left;

    outline: 0px solid #000;
    outline-offset: 2px;


}

.searchbar img {
    width: 50px;
    padding-right: 1%;
}

.searchbar button {
    height: 55px;
    width: 56px;
    padding-right: 8%;
    background-color: transparent;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    border: none;

}

.searchSection h1 {
    color: #e3fdfd;
    font-size: 44px;
}

.eventsection {
    height: 80vh;
    width: 100vw;
    background-color: #cbf1f5;
    border-top-left-radius: 300px;
    text-align: center;
    overflow: hidden;
}

.offersection {

    height: 100vh;
    width: 100vw;
    z-index: 1;
    background-color: white;
    text-align: center;

}

.addPropertysection {
    height: 450px;
    width: 100vw;
    text-align: center;
    background-color: #e3fdfd;
    display: flex;
    flex-direction: row;

    z-index: 1;

}

.eventsection h1,.offersection h1{
    font-size: 45px;
}

.texts {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    gap: 0.5%;
}

.texts button {
    width: 20%;
    height: 15%;
    font-size: 20px;
    font-weight: 700;
    margin-left: 37%;
    color: #e3fdfd;
    border-radius: 25px;
    background-color: #0ca4a5;
}
.texts h1{
    font-size: 50px;
}

.image {
    width: 80%;
    height: 120%;

    display: flex;
    justify-content: center;
    justify-items: center;

    padding: 5%;
}

.image img {
    background-color: antiquewhite;
    height: 80%;
    width: 80%;
    justify-items: center;
}

/*div containing the 4 input feilds*/
.searches {
    display: flex;
    width: 75%;
    margin-left: 5%;

}

.searches input {
    border-right: 2px solid #2596be;
    text-align: center;
    height: 75%;
    margin-top: 0.8%;
}

.Main {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    gap: 10.5%;

}

.Main button:hover {
    background-color: rgba(255, 255, 255, 0.833);
    
}
.Main button:active {
    background-color: rgba(24, 190, 190, 0.56)
    
}

.Main button {
    margin-left: 2%;
    height: 62px;
}


@media (max-width:967px) {
    .searchbar {
        width: 90%;
        margin-left: 5%;
    }

    .searchbar img {
        width: 40px;
    }

    .eventsection {
        border-top-left-radius: 0;
    }

    .eventsection h1 {
        font-size: 30px;
    }

    .offersection h1 {
        font-size: 30px;
    }

    .texts h1 {
        text-align: left;
        font-size: 40px;
        padding-left: 50px;
    }

    .texts button {
        width: 50%;
    }
}

@media (max-width: 500px) {
    .searchbar {
        width: 90%;
        margin-top: 20%;
    }

    .searchSection {
        padding-top: 20%;
    }

    .searchSection h1 {
        padding-top: 20%;
        font-size: 30px;
    }

    .image {
        display: none;
    }
}