* {
    font-family: 'Arial Narrow Bold', sans-serif;
}

.loginPage {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;

    border: 2px solid black;

}

.registerpage {
    height: 100vh;
    display: grid;
    align-items: center;
}

body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.registerpage h1,
.loginPage h1,.signuppage h1 {
    text-align: center;
    font-size: 64px;
    color: #1A1B41;
}

button {
    background: none;
    border: none;
}

.login main {

    margin-top: 100px;
}

.card {
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 500px;
    height: 400px;
    background-color: #E3FDFD;
    text-align: center;
    padding: 20px;
    color: #1A1B41;
    border: 5px solid #71C9CE;
    border-radius: 25px;
    transition: 0.3s;
}

.card h1 {
    font-size: 48px;
}

.card p {
    font-size: 24px;
}

.card:hover {
    background-color: #71C9CE;
    border: 5px solid #E3FDFD;
    box-shadow: 0px 10px 5px #71C9CE;
    scale: 1.1;
}

.card:hover h1 {
    color: #E3FDFD;
}

.card:hover p {
    color: #E3FDFD;
}

.horizontal {
    margin-top: 100px;
    display: flex;
    justify-content: space-evenly;
}

.login{
    position: absolute;
    width: 32%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 50px;
    font-size: 20px;
    height: 55vh;
    color: #1A1B41;
}
.signUp {
    position: absolute;
    width: 40%;
    top: 38%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 50px;
    font-size: 20px;
    height: 55vh;
    color: #1A1B41;
}

.agreement{
    display: flex;
    justify-content: left;
    gap: 2%;
     margin-left: 2%;
     height: 50%;
}
input[type='checkbox']{
    margin-top: -1.5%;
}

.loginPage span {
    font-size: 15px;
    text-align: center;
    margin: 22%;
}

.login fieldset,
.signUp fieldset {
    background-color: rgba(227, 253, 253, 0.8);
    padding: 30px;
    border-radius: 24px;
    border: 2px double #71C9CE;
    }
.signuppage fieldset{
    margin-bottom: 4%;
    box-shadow: 0px 10px 30px #71C9CE;
}

.login fieldset legend,
.signUp legend {
    font-weight: 700;
    font-size: 32px;
}

.login input,
.signUp input {
    height: 20px;
    border: none;
    margin-bottom: 40px;
    width: 90%;
    height: 44px;
    border-bottom: 2px solid #71C9CE;
    border-radius: 5px;
    background-color: #E3FDFD;
}

.login a {
    font-size: 14px;
    color: rgb(131, 131, 218);
}

.login input:focus {
    border: 2px solid #71C9CE;
}

.login input[type='submit'],
.signUp input[type='submit'] {
    margin: 10px;
    border-radius: 15px;
    font-size: 17px;
    text-align: center;
    background-color: white;
    transition: 0.3s;
    border: 1px double #71C9CE;
}

input[type='submit']:hover {
    background-color: #71C9CE;
    color: #E3FDFD;
}

.signUp {
    width: 50%;
}

.notFound {
    background: url('../images/wave.png');
    padding: 270px 0;
    perspective: 1000px;
    perspective-origin: 50% 50%;
    text-align: center;
    margin-top: 200px;
    margin-bottom: 0;
    animation: animate 10s linear infinite;
    overflow: hidden;
}

@keyframes animate {
    from {
        background-position: -2880px 0;
    }

    to {
        background-position: 2880px 0;
    }
}

.house {
    position: absolute;
    top: 200px;
}

.bg {
    position: fixed;
    width: 100%;
    z-index: -1;
}


@media (max-width: 967px) {
    h1 {
        font-size: 48px;
    }

    .login {
        width: 70%;
        top: 70vh;
        height: 90vh;
    }

    .bg {
        top: 70vh;
    }

    .signUp {
        width: 80%;
    }

    .signUp input {
        background-color: white;
    }

    legend {
        text-align: center;
    }
}

@media (max-width: 500px) {

    .horizontal {
        justify-content: center;
        flex-direction: column;

    }

    h1 {
        font-size: 48px;
    }

    .login {
        width: 90%;
        top: 70vh;
        height: 100vh;
    }

    .login fieldset,
    .signUp fieldset {
        border-radius: 12px;
    }

    .bg {
        display: none;
    }

    .signUp {
        width: 100%;
    }

    .signUp input,
    .login input {
        background-color: white;
    }

    legend {
        text-align: center;
    }
}