.MyPropertyCard {
    position: relative;
    background-color: #cbf1f5;
    border: 6px double #71c9ce;
    
  

    display: flex;
    flex-direction: row;
   
    flex: 0 50%;
 
    height: 400px;
    padding: 0.5%;
    margin-top: -1%;
    border-radius: 20px;
    
    box-shadow: 14px 8px 14px rgba(0, 0, 0.5, 0.5);
}


 
/* image on the card */
.MyPropertyCard #propImg {


    height: 100%;
    width: 100%;

    background-color: white;

    border-radius: 20px;
    text-align: center;

    position: relative;
}

.MyPropertyCard .image {
    height: 100%;
    width: 60%;
    
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    gap: 1%;
}


.MyPropertyCard .information {
    display: flex;
    flex-direction: column;
   
    width: 100%;
    padding-left: 3%;
    padding-bottom: 2%;
    gap: 0.5%;
}

.MyPropertyCard button {
    width: 130px;
    background-color: #28a745;
    color: white;
    border: 2px double whitesmoke;
    border-radius: 10px;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
}
.buttons{
    display: flex;
 
    gap: 4%;
    justify-content: center;
    align-items: center;
}
button[type='delete']{
    background-color: red;
    border: 2px double white;
}

.MyPropertyCard button:hover{
    opacity: 0.9;
    scale: 1.01;
}

.myproperties .search {
    display: flex;
    align-items: center;
    background-color: #e3fdfd; 
    border-radius: 25px; 
    padding: 10px 15px;
    width: 80%; 
    max-width: 600px; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.myproperties h2{
    color:#0ca4a5
}
 
.myproperties .search img {
    width: 30px; 
    height: 30px; 
    margin-right: 10px;
}


.myproperties .search input {
    border: none; 
    outline: none; 
    background: transparent; 
    flex: 1; 
    font-size: 20px; 
    padding: 5px; 
    border-radius: 25px; 
}


.myproperties .search input::placeholder {
    color: #aaa;
    font-style: italic;
}

.myproperties {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 3%;
}

.myproperties .SearchBar {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 20px;
    border-bottom: 3px solid #cbf1f5; /* For debugging, can be removed later */
    position: sticky; 
    top: 6%;
    z-index: 2;
}

.myproperties .cardlist {
    flex: 1; 
  
    display: flex;

    height: 100vh;
   
    gap: 2%;
    background-color: white;
    justify-content: center;
    padding: 5%;
   
    overflow: auto; 
}