.account{
    display: block;
}
.AccountPage{
    display: flex;

    height: 100vh;
    width: 100vw;
}
.accountInfo img{
    position: relative;  
    width: 100%;
    height: 100%;
    object-fit: fill;
    object-position: center; 
  }
.AccountPage .sideBar{/*side bar in account page.*/
    background-color: #e3fdfd;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 5%;
  
    justify-items: center;

    padding-top: 130px;
    width: 18%;
    height: 100%;
    color: rgb(62, 62, 62);

}
.AccountPage .sideBar a{
    margin-bottom: 30px;
    text-align: left;
    padding-left: 15%;
    font-size: 23px;
}
.links{
color: black;
}
.links:active{
    background-color: aliceblue;
}
.AccountPage .sideBar a:hover{
    background-color: #e3fdfd;
}

.sideBar a.edit{
    margin-top: 50%;
}
.AccountPage .sideBar button{
    margin-top: 30%;
    text-align: left;
    padding-left: 15%;
    font-size: xx-large;
    border: none;
    background-color: transparent;
}
.accountBanner{/*card with account pic,name and account type*/
    width: 75vw;
    height: 30vh;

    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 2%;

    margin-left: 3%;

    margin-top: 5%;

}
.Propic{/*Account pic*/
    width: 18%;
    height:85%;
    
    margin-left: 4%;
    margin-top: 2%;

    border-radius: 50%;
    background-color: white;

    background-size: cover; 
    background-position: center; 
    background-repeat: no-repeat;
    overflow: hidden;
    border :5px double #19c7d0;
}


.info{/*Account name and type labels.*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;

    /* border: 2px solid black; */
}
.AccountPage h1,.AccountPage h2,.AccountPage h3{
    /* border: 2px solid black; */
    text-align: center;
}

.AccountPage .historyTable{
    width: 85vw;
    height: 40vh;
    padding: 1%;
    
    margin-left: 5px;
    background-color: #e3fdfd;

    overflow: auto;
}
.AccountPage table{
    table-layout: fixed;
    font-size: larger;
    background-color: white;

    width: 100%;
    margin-left: 0.5%;
    height: fit-content;

    border: 1px solid black;
}

.editForm{
    margin-top: 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    justify-items: center;
}

.editForm input{
    height: 20px;
    border: none;
    margin-bottom: 40px;
   
    width: 90%;
    height: 44px;
    border-bottom: 2px solid #71C9CE;
    border-radius: 5px;
    background-color: #E3FDFD;
}

.editForm fieldset{
    border-radius: 20px;
    padding: 40px;
    width: 50vw;
    
}
.editForm input,.editForm label,.editForm legend{
    margin-left: 5%;
}

.editForm fieldset legend{
    font-size: 26px;
    font-weight: bold;
}
.editForm input[type="submit"]{
    background-color: green;
    color: white;
    border: 2px double rgba(255, 255, 255, 0.886);
    border-radius: 30px;
    height: 5%;
    font-size: 22px;
}
.editAcc{
    margin-bottom: 30px;
}

@media (max-width: 967px) {
    .AccountPage .sideBar{
        position: fixed;
        flex-direction: row;
        width: 100vh;
        height: 20%;
        color: rgb(62, 62, 62);
        
    }

    .AccountPage .sideBar a{
        font-size: 16px;
        margin-bottom: 0;
    }

    .AccountPage .account{
        margin-top: 200px;
    }


}

@media (max-width: 500px) {
    .AccountPage .sideBar{
        width: 100vh;
        height: 20%;
        color: rgb(62, 62, 62);
        
    }

    .AccountPage .sideBar a{
        font-size: 16px;
        padding-left: 20px;
    }

    .AccountPage .account{
        margin-top: 200px;
    }
}