.propertyListPage {
    /* border: 2px solid black; */
    /*this is just a temporary border to see the scope and size of the page.(i'll remove it later)*/

    height: 100vh;
    width: 100%;

    display: flex;
    flex-direction: column;
    margin-top: 5%;
    top: 3%;
    height: 100%;
    position: sticky;
     /*this is so the page stays put and doesnt move around the header and footer*/

}
  
.propertyListPage .cardList {   /* container that displays property cards searched. */

    display: flex;
    flex-wrap: wrap; 
    gap: 6%;
    background-color: white;
    margin-top: 5.83%;
    justify-content: center;

    position: sticky;
    top: 0;
    bottom: 0;
    padding: 5%;
    z-index: -1;
    height: 100vh;
    overflow: auto;
}


@media (max-width: 500px){
    .propertyListPage .cardList { 
        flex-direction: column;
        flex-wrap: nowrap;
        gap: 2%;
        margin-top: 60vh;
        justify-items: center;
        align-items: center;
        padding: 0;
    }
}